
    <template>
      <section class="market-doc-container">
        <h2>Loading 加载中</h2>
<h3>基本使用</h3>
<p>支持自定义指令的方式使用loading。</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-loading__demo&quot;&gt;
    &lt;div
      class=&quot;doc-loading__area&quot;
      v-loading=&quot;isLoading&quot;&gt;
    &lt;/div&gt;
    &lt;div class=&quot;doc-loading__operate&quot;&gt;
      &lt;mku-switch v-model=&quot;isLoading&quot;&gt;&lt;/mku-switch&gt;
      &lt;span&gt;开启/关闭Loading&lt;/span&gt;
    &lt;/div&gt;

    &lt;mku-alert
      style=&quot;margin-top: 10px;&quot;
      title=&quot;自定义属性：&quot;
      content=&quot;mku-loading-textz自定义提示内容；mku-loading-text-color自定义文本颜色；mku-loading-icon-color自定义图标颜色；mku-loading-background自定义背景色&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      isLoading: true
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>自定义</h3>
<p>支持自定义text文本、background</p>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-loading__demo&quot;&gt;
    &lt;div
      class=&quot;doc-loading__area&quot;
      v-loading=&quot;isLoading&quot;
      mku-loading-text=&quot;自定义的加载提示～&quot;
      mku-loading-text-color=&quot;#fff&quot;
      mku-loading-icon-color=&quot;#fff&quot;
      mku-loading-background=&quot;rgba(0, 0, 0, .7)&quot;&gt;
    &lt;/div&gt;
    &lt;div class=&quot;doc-loading__operate&quot;&gt;
      &lt;mku-switch v-model=&quot;isLoading&quot;&gt;&lt;/mku-switch&gt;
      &lt;span&gt;开启/关闭Loading&lt;/span&gt;
    &lt;/div&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      isLoading: true
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>全屏Loading</h3>
<p>作为指令打开全屏loading时，需要screen修饰符。
Js调用打开全屏loading时，需要设置screen属性。
Js调用创建的全屏loading是一个单例。</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-loading__demo&quot;&gt;
    &lt;mku-button
      type=&quot;primary&quot;
      v-loading.screen=&quot;isLoading&quot;
      mku-loading-text=&quot;自定义的加载提示～&quot;
      mku-loading-text-color=&quot;#fff&quot;
      mku-loading-icon-color=&quot;#fff&quot;
      mku-loading-background=&quot;rgba(0, 0, 0, .7)&quot;
      @click=&quot;openLoading&quot;
    &gt;
      指令
    &lt;/mku-button&gt;

    &lt;mku-button type=&quot;primary&quot; @click=&quot;openLoading2&quot;&gt;
      Js调用
    &lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    openLoading () {
      this.isLoading = true
      setTimeout(() =&gt; {
        this.isLoading = false
      }, 2000)
    },
    openLoading2 () {
      const loading = this.$loading({
        screen: true,
        background: 'rgba(255, 255, 255, .7)'
      })
      setTimeout(() =&gt; {
        loading.close()
      }, 1500)
    },
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>通过JS调用</h3>
<p>支持通过JS打开全屏和容器的Loading效。</p>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-loading__demo&quot;&gt;
    &lt;div class=&quot;doc-loading__area&quot; id=&quot;doc-loading__area&quot;&gt;
      容器Loading竟会在此展示
    &lt;/div&gt;

    &lt;div class=&quot;doc-loading__operate&quot;&gt;
      &lt;mku-button
        style=&quot;margin-right: 20px;&quot;
        @click=&quot;openLocalLoading&quot;&gt;
        打开Loading
      &lt;/mku-button&gt;
    &lt;/div&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    openLocalLoading () {
      const loading = this.$loading({
        target: '#doc-loading__area'
      })
      setTimeout(() =&gt; {
        loading.close()
      }, 1500)
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Loading 参数</h3>
<h4>Js调用时的参数Options</h4>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必选</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">target</td>
<td style="text-align:left">Loading的目标对象，非screen模式必填</td>
<td style="text-align:left"><code>String | Node节点</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">screen</td>
<td style="text-align:left">是否作为全屏loading</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">text</td>
<td style="text-align:left">Loading的文字提示</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>加载中</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">text-color</td>
<td style="text-align:left">Loading的文字颜色</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>#515a6e</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">icon-color</td>
<td style="text-align:left">Loading的图标颜色</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>#2d8cf0</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">background</td>
<td style="text-align:left">Loading的背景色</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>#fff</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>

      </section>
    </template>
